import React from 'react';
import Typewriter from 'typewriter-effect';
import { Link } from 'react-router-dom'

const LandingPage = () => (
  <div className="landing-main">
  <div className="landingpage">
<div className="landingpage-left">
<h1 className="logo">Involve <span>Teacher</span></h1>
<div className="typing">
<h1 >
<Typewriter
  options={{
    strings: ['Learn', 'Collaborate', 'Improvise'],
    autoStart: true,
    loop: true,
  }}
/>
</h1>
</div>
<h2 className="landing-description">A digital tool that helps to create learning experiences for the professional development of teachers in Nigeria.</h2>
<div className="landingpage-buttons">
<Link className="button button__landing" to="/signup" >
<h3>Signup</h3>
</Link>
<Link className="button button__landing" to="/login" >
<h3>Login </h3>
</Link>
</div>
</div>
<div  className="landingpage-right">
</div>
</div>
  </div>

);

export default LandingPage